.coaches__wrapper {
  position: relative;
  margin: 0 auto;
  padding: 105px 0;
  max-width: 1160px;

  @include vp-1199 {
    padding: 50px 0 100px;
  }

  @include vp-767 {
    padding: 33px 0 70px 0;
  }
}

.coaches__title {
  margin: 0 0 50px 0;
  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 54px;
  line-height: 64.8px;
  letter-spacing: 1.7px;

  @include vp-1199 {
    margin: 0 0 31px 0;
    font-size: 42px;
    line-height: 50.4px;
    letter-spacing: 1.3px;
    text-align: center;
  }

  @include vp-767 {
    margin: 0 0 42px 0;
    font-size: 32px;
    line-height: 38.4px;
    letter-spacing: 1px;
    text-align: center;
  }
}

.coaches__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.coaches__swiper {
  @include vp-1199 {
    margin: 0 56px;
  }

  @include vp-767 {
    margin: 0 30px;
  }
}

.coaches__item {
  position: relative;
  color: $color-default-white;
  background-color: $color-dark-cerulian;
  overflow: hidden;

  @include vp-1199 {
    min-width: 268px;
    min-height: 294px;
    aspect-ratio: 268/294;
  }

  @include vp-767 {
    min-width: 226px;
    min-height: 274px;
    aspect-ratio: 230/274;
  }
}

.coaches__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.coaches__item h3 {
  position: absolute;
  bottom: 23px;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  padding: 0 20px;
  width: 100%;
  max-width: 260px;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  text-align: center;

  @include vp-1199 {
    left: 51.5%;
    max-width: 100%;
  }
}

.coaches__item:hover h3,
.coaches__item:focus h3 {
  display: none;
}

.coaches__information {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 55px 30px;
  background-color: $color-sapphire-coaches;
  overflow-y: hidden;
  transform: scale(1.3);
  opacity: 0;
  transition: transform 0.5s ease 0s;

  @include vp-767 {
    padding: 60px 9px;
  }
}

.coaches__item:hover .coaches__information,
.coaches__item:focus .coaches__information {
  opacity: 1;
  transform: scale(1);
}

.coaches__information h4 {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-align: center;

  @include vp-1199 {
    margin: 0 9px 0 0;
  }

  @include vp-767 {
    margin: 0;
  }
}

.coaches__information p {
  margin: 0 0 33px 0;
  text-align: center;
  color: $color-spindle;

  @include vp-1199 {
    margin: 0 7px 33px 0;
  }

  @include vp-767 {
    margin: 0 3px 33px 0;
  }
}

.coaches__information ul {
  font-size: 14px;
  line-height: 21px;
}

.coaches__information li {
  margin: 0 0 13px 0;
}

.coaches__button {
  position: absolute;
  top: 55%;
  width: 56px;
  height: 56px;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  cursor: pointer;

  &:hover,
  &:focus {
    border-radius: 50%;
    border: 1px solid $color-sapphire;
  }

  &:active {
    border-radius: 50%;
    background-color: $color-sapphire;
  }

  @include vp-1199 {
    top: 47%;
  }

  @media (min-width: 1200px) and (max-width: 1279px) {
    &:hover,
    &:focus {
      border: none;
    }
  }

  @include vp-767 {
    top: 53%;
    width: 10px;
    height: 19px;

    &:hover,
    &:focus {
      border: none;
    }

    &:active {
      background-color: transparent;
    }
  }

  &--next {
    vertical-align: middle;
    right: -76px;

    &:active svg {
      color: #fff;
    }

    @include vp-1199 {
      right: -20px;
    }

    @media (min-width: 1200px) and (max-width: 1279px) {
      right: -55px;
    }

    @include vp-767 {
      right: 3px;
    }
  }

  &--prev {
    vertical-align: middle;
    left: -76px;
    transform: rotate(180deg);

    &:active svg {
      color: #fff;
    }

    @include vp-1199 {
      left: -20px;
    }

    @media (min-width: 1200px) and (max-width: 1279px) {
      left: -55px;
    }

    @include vp-767 {
      left: 3px;
    }
  }
}

.coaches__button svg {
  position: absolute;
  top: 60%;
  left: 45%;
  transform: translate(-50%, -50%);

  @include vp-767 {
    top: 75%;
  }
}

.coaches__button.is-disabled {
  opacity: 30%;
  pointer-events: none;
  cursor: default;
}
