.footer__navigation li {
  position: relative;
  font-size: 14px;
  line-height: 16.4px;
}
.footer__navigation li::before {
  content: "";
  position: absolute;
  bottom: 8px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: rgb(255, 255, 255);
  transform: scale(0);
  transform-origin: center;
  transition: 0.3s;
}
@media (hover: hover) {
  .footer__navigation li:hover:not(.focus-visible)::before {
    transform: scale(1);
  }
}
.footer__navigation li:active::before {
  display: none;
}
.footer__navigation li:focus a {
  outline: solid 1px rgb(255, 255, 255);
}
.footer__navigation li:focus a::before {
  display: none;
}
