.free-visit__title {
  margin: 0 0 23px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 800;
  font-size: 54px;
  line-height: 70px;
  letter-spacing: 2.5px;
}
@media (max-width: 1199px) {
  .free-visit__title {
    margin: 0 0 32px 1px;
    font-weight: 700;
    font-size: 42px;
    line-height: 50.4px;
    letter-spacing: 1.5px;
  }
}
@media (max-width: 767px) {
  .free-visit__title {
    margin: 0 0 34px 0;
    font-weight: 600;
    font-size: 32px;
    line-height: 38.4px;
    letter-spacing: 0.96px;
  }
}

.contacts {
  border-top: 1px solid #74819c;
}
@media (max-width: 1199px) {
  .contacts {
    border: none;
  }
}

.contacts__wrapper {
  max-width: 683px;
  margin: 0 auto 0 0;
  padding: 56px 159px 43px 120px;
}
@media (max-width: 1199px) {
  .contacts__wrapper {
    margin: 0 auto;
    padding: 61px 90px 38px;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper {
    margin: 0;
    padding: 64px 15px 39px;
    max-width: 100vw;
  }
}

.contacts__title {
  margin: 0 0 35px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 65.8px;
  letter-spacing: 1.5px;
}
@media (max-width: 1199px) {
  .contacts__title {
    margin: 0 0 35px 0;
    font-size: 42px;
    line-height: 50.4px;
    letter-spacing: 1.2px;
  }
}
@media (max-width: 767px) {
  .contacts__title {
    margin: 0 0 36px 0;
    font-size: 32px;
    line-height: 38.4px;
    letter-spacing: 1px;
  }
}

.contacts__lists address,
.contacts__lists p,
.contacts__lists a {
  margin: 0;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 22px;
  color: #102654;
}
@media (max-width: 1199px) {
  .contacts__lists address,
.contacts__lists p,
.contacts__lists a {
    font-size: 18px;
    line-height: 39.4px;
  }
}

.contacts__lists {
  margin: 0;
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 120px;
}
.contacts__lists::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 234px;
  width: 1px;
  height: 79%;
  background-color: #74819c;
}
@media (max-width: 1199px) {
  .contacts__lists {
    margin: 0;
    gap: 96px;
  }
  .contacts__lists::before {
    top: 6px;
    left: 266px;
    height: 77%;
  }
}
@media (max-width: 767px) {
  .contacts__lists {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .contacts__lists::before {
    content: none;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.contacts__item {
  margin: 0 0 19px 0;
  min-width: 170px;
}
@media (max-width: 1199px) {
  .contacts__item {
    margin: 0 0 19px 0;
    min-width: 218px;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    margin: 0 0 18px 0;
  }
}

.contacts__item h3 {
  margin: 0;
  font-size: 16px;
  line-height: 25px;
}
@media (max-width: 1199px) {
  .contacts__item h3 {
    font-size: 20px;
  }
}

.blocks-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 50%);
}
@media (max-width: 1199px) {
  .blocks-wrapper {
    grid-template-columns: 1fr;
  }
}

